import { Toast } from 'vant'
import { getToken } from './utils'
import store from './store'
import router from './router'

const whiteList = ['/register', '/login'] // no redirect whitelist

/**  **/
router.beforeEach(async (to,from,next) => {//  Vue.beforeEach(function(to,form,next){}) /**/
  document.title = to.meta.title
  if (to.meta.requireAuth ) {  // 
      if (getToken()) { 
        
        const hasGetUserInfo = store.state.name
        if (hasGetUserInfo) {
          next()
        } else {
          try {
            // 
            await store.dispatch('getUserInfo')
            next()
          } catch (error) {
            // remove token and go to login page to re-login
            await store.dispatch('resetToken')
            Toast(error || 'Has Error')
            // next(`/login?redirect=${to.path}`)
          }
        }
      }
      else {
        try {
          // user_token
          await store.dispatch('getUserToken')
          next()
        } catch (error) {
          await store.dispatch('resetToken')
          Toast(error || 'Has Error')
        }
					// 
          // next({
          //     path: '/register',
          //     query: {redirect: to.name}  // path
          // })
      }
  }
  else {
    next();
  }
})