import axios from 'axios'
import qs from 'qs'
import { Toast } from 'vant'
import router from '@/router'




const http = axios.create({
	baseURL: 'http://54.193.133.101:3000',
  timeout:100 * 1000, // 
})

http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
let datapublic={
	lang:'zh-CN',
	user_token:localStorage.getItem('user_token')
}

http.interceptors.request.use(
	(originConfig) => {
		const reqConfig = { ...originConfig }

		// 
		if (!reqConfig.url) {
			/* eslint-disable-next-line */
			console.error('request need url')
			throw new Error({
				source: 'axiosInterceptors',
				message: 'request need url',
			})
		}

		if (!reqConfig.method) {
			//  get 
			reqConfig.method = 'get'
		}

		// 
		reqConfig.method = reqConfig.method.toLowerCase()

		// 
		if (reqConfig.method === 'get') {
			if (!reqConfig.params) {
				// 
				reqConfig.params = reqConfig.data || {}
			}
		} else if (reqConfig.method === 'post') {
			if (!reqConfig.data) {
				// 
				reqConfig.data = reqConfig.params || {}
			}
		} else {
			// 
			/* eslint-disable-next-line */
			console.warn(`: ${reqConfig.method}, `)
		}

		// token
		const accessToken = localStorage.getItem('user_token')
		if (accessToken) {
			reqConfig.headers.Authorization = accessToken
		}

		//  formData 
		if (reqConfig.method === 'post') {
			let hasFile = false
			Object.keys(reqConfig.data).forEach((key) => {
				if (typeof reqConfig.data[key] === 'object') {
					const item = reqConfig.data[key]
					if (
						item instanceof FileList ||
						item instanceof File ||
						item instanceof Blob
					) {
						hasFile = true
					}
				}
			})

			//  FormData 
			if (hasFile) {
				const formdata = new FormData()
				Object.keys(reqConfig.data).forEach((key) => {
					formdata.append(key, reqConfig.data[key])
				})
				reqConfig.data = formdata
			} 
			else {
				reqConfig.data = qs.stringify(reqConfig.data)
			}
		}

		return reqConfig
	},
	(error) => {
		
		Promise.reject(error)
	}
)


http.interceptors.response.use(
	(res) => {
		
		let { msg } = res.data
		let statusCode = res.status

		if (statusCode.toString().charAt(0) === '2') {
			return res
		}

		return new Promise((resolve, reject) => {
			//  handleError: truetry catch
			if (res.config.handleError) {
				return reject(res)
			}

			// /
			// if (statusCode === 401) {
			//   removeToken()
			//   msg = ''
			//   router.replace({
			//     path: '/login',
			//     query: {
			//       redirect: router.currentRoute.fullPath,
			//     },
			//   })
			// } else if (statusCode.toString().charAt(0) === '5') {
			//   msg = ''
			// }
			
			Toast(msg)
			reject()
		})
	},
	(error) => {
		if (!error.response) {
			// 
			if (
				error.code === 'ECONNABORTED' &&
				error.message.includes('timeout')
			) {

				Toast('')
			} else {
				Toast('')
			}
			return
		}
		// console.log(error.response.data)
		Toast(error.response.data.msg)
		return Promise.reject(error)
	}
)


export function get(url, params = {}) {
	let data={...params,...datapublic}
	return http({
		method: 'get',
		url,
		data,
	})
}


export function post(url, data = {}, params = {}) {
	// let dataA={...params,...datapublic}
	return http({
		method: 'post',
		url,
		data,
		params,
	})
}

export function put(url, data = {}, params = {}) {
	return http({
		method: 'put',
		url,
		data,
		params,
	})
}

export function remove(url, params = {}) {
	return http({
		method: 'delete',
		url,
		params,
	})
}

export default http